.nav-link {
  padding: 0 10px !important;
  font-weight: 800 !important;
  font-size: 15px !important;
}
body {
  font-family: "Odibee Sans", cursive;
}
.error {
  font-family: "Odibee Sans", cursive !important;
  font-size: 40px !important;
}
.navbar-brand {
  font-weight: 800 !important;
  font-size: 25px !important;
  color: skyblue !important;
}

.link_text {
  color: skyblue !important;
}
.banner {
  background-color: skyblue;
  border-radius: 10px;
}
.banner .container {
  padding: 30px;
  text-align: center;
}
.logo-font {
  font-family: Cambria, Cochin, Georgia, Times, "Times New Roman", serif;
  font-weight: 900;
  font-size: 70px;
  color: aliceblue;
}
.banner .container p {
  color: aliceblue;
}
.nav-link img {
  width: 25px;
  height: 25px;
}
.profileicon {
  width: 32px;
  height: 32px;
  vertical-align: middle;
  display: inline-block;
  border-radius: 30px;
}
@media only screen and (min-device-width: 480px) {
  body {
    font-family: "Odibee Sans", cursive;
  }
}

@media only screen and (max-device-width: 480px) {
  /* .home_nav{
    display: none;
  } */
  .login_email {
    display: none;
  }
}
.profile_name {
  color: yellowgreen;
  padding: 5px;
}
.profile_message {
  padding: 10px;
  text-align: center;
  font-style: italic;
}
.feed_like_section {
  justify-content: space-evenly;
}
.feed_like_section div {
  display: flex;
}
.article-meta {
  display: block;
}
.info {
  margin: 0 1.5rem 0 0.3rem;
  display: inline-block;
  vertical-align: middle;
  line-height: 1rem;
}
.author {
  display: block;
}
.date {
  display: block;
  color: cornflowerblue;
}
.article-preview {
  padding: 20px;
  background-color: aliceblue;
  border: 1px solid #ffffff;
}
.footer_section {
  display: flex;
  flex-direction: row;
  width: 100%;
  justify-content: space-evenly;
  padding: 10px;
}
.footer_section div {
  display: flex;
}
.description {
  padding: 20px;
}
.footer_section div {
  cursor: pointer;
}

.comments :hover {
  text-decoration: underline;
}
.addExp {
  position: fixed;
  right: 5px;
  bottom: 0px;
  border-radius: 5px;
  background-color: aqua;
}
.pro_Container {
  margin: 20px;
  height: 400px;
  width: 72%;
  display: flex;
  flex-direction: column;
}
.pro_Container div {
  display: flex;
  flex-direction: row;
  color: rgba(0, 0, 0, 0.5);
}
